<template>
  <div>
      <h1 class="title">{{title}} Director</h1>

      <div class="field">
          <label class="label">First Name</label>
          <div class="control">
              <input class="input" type="text" v-model="director.firstName" placeholder="First Name" />
          </div>
      </div>

      <div class="field">
          <label class="label">Last Name</label>
          <div class="control">
              <input class="input" type="text" v-model="director.lastName" placeholder="Last Name" />
          </div>
      </div>

      <div class="field">
          <label class="label">Date of Birth</label>
          <div class="control">
              <input class="input" type="text" v-mask="'####-##-##'" v-model="director.dateOfBirth" placeholder="YYYY-MM-DD" />
          </div>
      </div>

      <div class="field is-grouped">
          <p class="control">
              <button v-on:click="cancel" class="button">Cancel</button>
          </p>
          <p class="control">
              <button v-on:click="save" class="button is-primary">Save</button>
          </p>
      </div>

  </div>
</template>

<script>
export default {
  name: 'AddDirector',
  data: () => ({
      director: {
          firstName: "",
          lastName: "",
          dateOfBirth: ""
      },
      title: "Add"
  }),
  methods: {
      cancel() {
          this.$router.push({path: '/directors'});
      },
      async save() {
        const response = this.title === "Add" 
                ? await this.$http.post('http://3.149.27.3:8080/api/directors/', this.director)
                : await this.$http.put(`http://3.149.27.3:8080/api/directors/${this.director.id}`, this.director)
          console.log(response);
          if (response.status === 200) {
              this.$router.push({path: '/directors'});
          }
      }
  },
  async mounted() {
      console.log(this.$route.params.id)
      if (this.$route.params.id) {
        this.title = "Update"
        const { data } = await this.$http.get(`http://3.149.27.3:8080/api/directors/${this.$route.params.id}`)
        this.director = data
      }

  }
}
</script>